import {
  Actor,
  CollisionType,
  Color,
  Engine,
  vec,
  ImageSource,
  Sprite,
  Animation,
  Loader,
} from "excalibur";

const imageSources = [
  new ImageSource(require("./zombie/zombie1.png")),
  new ImageSource(require("./zombie/zombie2.png")),
  new ImageSource(require("./zombie/zombie3.png")),
  new ImageSource(require("./zombie/zombie4.png")),
  new ImageSource(require("./zombie/zombie5.png")),
  new ImageSource(require("./zombie/zombie6.png")),
];

export const loader = new Loader(imageSources);

const sprites = imageSources.map((imageSource) => {
  return new Sprite({
    image: imageSource,
    sourceView: {
      x: 0,
      y: 0,
      width: 500,
      height: 500,
    },
    destSize: {
      width: 100,
      height: 100,
    },
  });
});

export const zombieAnimation = new Animation({
  frames: sprites.map((sprite) => {
    return {
      graphic: sprite,
      duration: 100,
    };
  }),
});
